* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  position: relative;
  min-height: 100%;
  font-size: 16px;
}

body {
  width: 100%;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Fira Sans", Helvetica, Arial, sans-serif;
  font-weight: 400;
  color: #fff;
  text-transform: uppercase;
  background-color: #171919;
}

img,
embed,
object,
video {
  max-width: 100%;
}

p {
  margin: 1.5625rem 0;
  font-size: .9375rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: .046875rem;
}

a {
  color: #fff;
  text-decoration: none;
  outline: none;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
}

.section {
  padding: 10rem 0 7.5rem;
}

.title {
  position: relative;
  display: inline-block;
  font-size: 5rem;
  font-weight: 800;
}

.title::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 5rem;
  height: 1px;
  content: '';
  background-color: #4af6cd;
}

.subtitle {
  display: flex;
  align-items: center;
  margin-bottom: 1.5625rem;
  font-size: 1.375rem;
  line-height: 2rem;
  letter-spacing: .458125rem;
  opacity: .5;
}

.subtitle::before {
  width: 5rem;
  height: 1px;
  margin-right: 1.5625rem;
  content: '';
  background-color: #fff;
  opacity: .5;
}

.subtitle_left::after {
  width: 5rem;
  height: 1px;
  margin-left: 1.5625rem;
  content: '';
  background-color: #fff;
  opacity: .5;
}

.subtitle_left::before {
  display: none;
}

.subtitle__text {
  font-size: 2.5rem;
  font-weight: 800;
}

.subtitle__text span {
  font-weight: 200;
}

.btn {
  font-size: 2.5rem;
  font-weight: 800;
  line-height: 2.25rem;
}

.btn span {
  position: relative;
  display: inline-block;
  padding: 1.0625rem 3.5625rem 1.0625rem 1.0625rem;
  margin-left: 1.1875rem;
  background-color: #4af6cd;
}

.btn span::before {
  position: absolute;
  top: 50%;
  left: -10px;
  width: 1.25rem;
  height: .0625rem;
  content: '';
  background-color: #fff;
}

.btn span::after {
  position: absolute;
  top: 50%;
  right: 1rem;
  width: 1.5625rem;
  height: 1.3125rem;
  content: '';
  background-image: url("..//img/arrow-right.svg");
  transform: translateY(-50%);
}

.animation__line {
  stroke-dasharray: 600;
  stroke-dashoffset: 600;
  animation: mapline 4s 3.5s;
  animation-fill-mode: forwards;
}

@keyframes mapline {
  to {
    stroke-dashoffset: 0;
  }
}

.animation__delay-1000ms {
  animation-delay: 1s;
}

.animation__delay-1500ms {
  animation-delay: 1.5s;
}

.animation__delay-2000ms {
  animation-delay: 2s;
}

.animation__delay-2500ms {
  animation-delay: 2.5s;
}

.animation__delay-3000ms {
  animation-delay: 3s;
}

